import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
import uniNoticeBar from '@/components/uni-notice-bar/uni-notice-bar.vue';
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
import wPicker from '@/components/w-picker/w-picker.vue';
export default {
	components: {
		wPicker
	},
	data() {
		return {
			is_child: '',
			is_miniBind: '',
			imgRemote: imgRemote,
			load: true,
			navBar: navBar,
			tabList: [{
				name: '待审核'
			}, {
				name: '备货中'
			}, {
				name: '配送中'
			}, {
				name: '已收货'
			}, {
				name: '已取消'
			}],

			activeTab: -1,

			activeTab: 1,
			lineShow: false,
			showOrderType: false,
			childListProps: {
				"label": "nickname",
				"value": "zid"
			},
			childList: [{
				"zid": "",
				"nickname": "当前账号",
			}],
			childzid: '',
			childtxt: '当前账号',
			isActive: '全部',
			orderTitle: '全部订单',
			type: 1,
			search_default: true,
			is_look: '',
			count: 0,
			orderList: [],
			orderInfo: '',
			page: 1,
			map: false,
		};
	},
	methods: {

		tabClick(data) {
			this.activeTab = data;
			this.isActive = data;
			this.page = 1;
			this.orderLista();
		},

		/**
		 * 确认收货
		 */
		receiveOrder(data, index) {
			var that = this;
			let dataId = data;
			let dataIndex = index;
			uni.showModal({
				title: '提示',
				content: '是否确认收货？',
				success: function(res) {
					if (res.confirm) {
						// console.log('用户点击确定');
						var id = dataId;
						var orderindex = dataIndex;
						var timeStamp = Math.round(new Date().getTime() / 1000);
						var obj = {
							appid: appid,
							id: id,
							timeStamp: timeStamp,
						}
						var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
						var data = {
							appid: appid,
							timeStamp: timeStamp,
							id: id,
							sign: sign,
						}
						rs.getRequests("receiveOrder", data, (res) => {
							if (res.data.code == 200) {
								uni.showToast({
									title: "确认收货成功",
									icon: 'none'
								})
								that.orderList.splice(index, 1)
								if (that.orderList.length <= 0) {
									that.orderLista()
								}
							} else {
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								})
							}
						})
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});

		},
		/**
		 * 页面跳转
		 */
		//支付
		play(data) {
			var oid = data;
			uni.navigateTo({
				url: '/pages/order/pay?oid=' + oid,
			})
		},
		orderDetailPage(url, item) {
			if (url == 'orderDetail') {
				uni.navigateTo({
					url: '/pages/order/orderdetail?orderItem=' + item.id
				})
			} else if (url == 'user') {
				rs.Toast("还未绑定微信,请去我的页面绑定微信")
				uni.navigateTo({
					url: '/pages/tabar/user'
				});
			}

		},
		/**
		 * 取消订单
		 */
		cancelOrder(data, index) {
			var that = this
			let dataId = data;
			let dataIndex = index;
			uni.showModal({
				title: '提示',
				content: '是否取消订单？',
				success: function(res) {
					if (res.confirm) {
						// console.log('用户点击确定');
						var id = dataId;
						var orderindex = dataIndex;
						var timeStamp = Math.round(new Date().getTime() / 1000);
						var obj = {
							appid: appid,
							id: id,
							timeStamp: timeStamp,
						}
						var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
						var data = {
							appid: appid,
							timeStamp: timeStamp,
							id: id,
							sign: sign,
						}
						rs.getRequests("cancelOrder", data, (res) => {
							console.log(res)
							if (res.data.code == 200) {
								uni.showToast({
									title: "取消订单成功",
									icon: 'none'
								})
								that.orderList.splice(index, 1)
								if (that.orderList.length <= 0) {
									that.orderLista()
								}
							} else if (res.data.code == 101) {
								uni.showToast({
									title: "订单已审核，不能取消",
									icon: 'none'
								})
							} else {
								uni.showToast({
									title: res.data.msg,
									icon: 'none'
								})
							}
						})
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		/**
		 * 查看物流
		 */
		ckwl(data) {

			var that = this;
			that.count++;
			console.log(that.count)
			if (that.count != 1) {
				return;
			}
			var id = data;
			if (id <= 0) {
				rs.Toast("无物流信息")
				return;
			}
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				id: id,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				timeStamp: timeStamp,
				id: id,
				sign: sign,
			}
			rs.postRequests("carPosition", data, (res) => {
				setTimeout(() => {
					that.count = 0;
				}, 1000)
				if (res.data.code == 200) {
					if (res.data.data != '') {
						var latitude = parseFloat(res.data.data.latitude);
						var longitude = parseFloat(res.data.data.longitude);
						if (res.data.data.latitude == '' || res.data.data.longitude == '') {
							rs.Toast("无物流信息")
						} else {







							uni.getLocation({
								type: 'gcj02', // 默认为 wgs84 返回 gps 坐标，gcj02 返回可用于 wx.openLocation 的坐标
								success: function(res) {
									that.map = true;

									let mapObj = that.bd_decrypt(longitude, latitude);

									uni.openLocation({
										latitude: mapObj.lat, // 纬度，范围为-90~90，负数表示南纬
										longitude: mapObj.lng, // 经度，范围为-180~180，负数表示西经										
										name: ' ',
										address: ''
									})
								}
							})
						}
					} else {
						rs.Toast("无物流信息")
					}
				} else {
					rs.Toast(res.data.msg)
				}
			})
		},
		//百度坐标转高德（传入经度、纬度）
		bd_decrypt(bd_lng, bd_lat) {
			var X_PI = Math.PI * 3000.0 / 180.0;
			var x = bd_lng - 0.0065;
			var y = bd_lat - 0.006;
			var z = Math.sqrt(x * x + y * y) - 0.00002 * Math.sin(y * X_PI);
			var theta = Math.atan2(y, x) - 0.000003 * Math.cos(x * X_PI);
			var gg_lng = z * Math.cos(theta);
			var gg_lat = z * Math.sin(theta);
			return {
				lng: gg_lng,
				lat: gg_lat
			}
		},
		/**
		 * 再来一单
		 */
		oneMoreTime(data) {
			let dataId = data;
			uni.showModal({
				title: '提示',
				content: '是否再来一单？',
				success: function(res) {
					if (res.confirm) {
						// console.log('用户点击确定');
						var that = this
						var id = dataId;
						var timeStamp = Math.round(new Date().getTime() / 1000);
						var obj = {
							appid: appid,
							id: id,
							timeStamp: timeStamp,
						}
						var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
						var data = {
							appid: appid,
							timeStamp: timeStamp,
							id: id,
							sign: sign,
						}
						rs.getRequests("oneMoreTime", data, (res) => {
							console.log(res)
							if (res.data.code == 200) {
								uni.showToast({
									title: "再来一单成功",
									duration: 2000,
									icon: 'none'
								})
								setTimeout(function() {
									uni.switchTab({
										url: '/pages/tabar/shopcart'
									})
								}, 1000);

							} else if (res.data.code == 102) {
								uni.showToast({
									title: "有下架商品",
									duration: 2000,
									icon: 'none'
								})
								setTimeout(function() {
									uni.switchTab({
										url: '/pages/tabar/shopcart'
									})
								}, 1000);
							} else {
								uni.showToast({
									title: res.data.msg,
									duration: 2000,
									icon: 'none'
								})
							}
						})
					} else if (res.cancel) {
						// console.log('用户点击取消');
					}
				}
			});
		},
		/**
		 * 获取子账号信息
		 */
		childInfo() {
			var that = this;
			that.childList = [{
				"zid": "",
				"nickname": "当前账号",
			}]
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				timeStamp: timeStamp,
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				timeStamp: timeStamp,
				sign: sign,
			}
			rs.getRequests("childInfo", data, (res) => {
				if (res.data.code == 200) {
					var childInfo = res.data.data;
					var arr = ["当前账号"];
					var num = [""];
					for (var i = 0; i < childInfo.length; i++) {
						that.childList.push(childInfo[i])
					}
				}
			})
		},
		//初始订单请求
		orderListb() {
			var that = this;
			that.orderList = [];
			that.search_default = true;

			var page = that.page;
			var type = that.type;
			if (that.isActive != "全部") {
				var status = that.isActive + 1;
			} else {
				var status = '';
			}
			var num = 10;
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				page: page,
				timeStamp: timeStamp,
				type: type
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				page: page,
				timeStamp: timeStamp,
				num: num,
				sign: sign,
				type: type,
				status: status,
			}
			rs.getRequests("orderList", data, (res) => {
				if (res.data.code == 200) {
					if (res.data.data.list.length > 0) {
						if (res.data.data.list.length <= 9) {
							that.load = false;
						} else {
							that.load = true;
						}

						that.search_default = true;
						that.is_look = res.data.data.is_look;
						that.orderInfo = res.data.data;
						that.orderList = res.data.data.list;
						// console.log(res.data.orderInfo)
					} else {

						that.search_default = false;
						that.load = '空';
					}
				}
			})
		},
		orderLista() {
			var that = this;
			that.orderList = [];
			that.search_default = true;
			that.load = true;
			var select_zid = that.childzid;
			if (that.isActive != "全部") {
				var status = that.isActive + 1;
			} else {
				var status = '';
			}

			var page = 1;
			var type = that.type;
			var num = 10
			var timeStamp = Math.round(new Date().getTime() / 1000);
			var obj = {
				appid: appid,
				page: page,
				timeStamp: timeStamp,
				type: type
			}
			var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			var data = {
				appid: appid,
				page: page,
				timeStamp: timeStamp,
				num: num,
				sign: sign,
				type: type,
				select_zid: select_zid,
				status: status,
			}
			rs.getRequests("orderList", data, (res) => {
				if (res.data.code == 200) {
					if (res.data.data.list.length > 0) {
						if (res.data.data.list.length <= 9) {
							that.load = false;
						} else {
							that.load = true;
						}
						that.search_default = true;
						that.is_look = res.data.data.is_look;
						that.orderInfo = res.data.data;
						that.orderList = res.data.data.list;
					} else {
						that.search_default = false;
						that.load = true;
					}
				}
			})
		},
		onChild(e) {
			this.childtxt = e.obj.nickname;
			this.childzid = e.obj.zid;
			this.page = 1;
			this.orderLista();
		},
		selectAccount() {
			this.$refs.account.show();
		},
		titleTab(data) {
			this.orderTitle = data;
			this.showOrderType = false;
			if (data == "未支付") {
				this.type = 2;
			} else if (data == "全部订单") {
				this.type = 1;
			}
			this.page = 1;
			this.lineShow = false;
			this.activeTab = 6;
			this.isActive = "全部";
			this.orderLista();
		},
		changeFirst(index) {
			this.load = true;
			this.isActive = index;
			this.lineShow = true;
			this.page = 1;
			this.orderLista();
		},
		rightClick() {
			this.showOrderType = true;
		},
		// //回到顶部
		goTop() { // 一键回到顶部
			uni.pageScrollTo({
				scrollTop: 0
			})
		},
	},
	/**
	 * 生命周期函数--监听页面显示
	 */
	onShow() {
		var that = this;
		that.is_child = uni.getStorageSync("is_child");
		that.is_miniBind = uni.getStorageSync("is_miniBind");
		that.childList = [{
			"zid": "",
			"nickname": "当前账号",
		}]
		if (!uni.getStorageSync("cdj_token")) {
			uni.navigateTo({
				url: '/pages/account/login'
			});
		} else {
			let aShow = app.aData.show;
			console.log(aShow)
			if (aShow == false) {
				that.orderList = [];
				that.childtxt = '当前账号';
				that.childzid = '';
				that.page = 1;
				that.type = 1;
				that.activeTab = 6;
				this.lineShow = false;
				that.orderTitle = '全部订单';
				that.isActive = '全部';
				that.orderListb() //订单列表
				that.goTop()
			}
			if (uni.getStorageSync("is_child") == 0) {
				that.childInfo();
				//用户信息
			}
		}
	},
	/**
	 * 生命周期函数--监听页面隐藏
	 */
	onHide: function() {
		if (this.map == true) {
			app.aData.show = true;
		} else {
			app.aData.show = false;
		}
	},
	/**
	 * 页面上拉触底事件的处理函数
	 */
	onReachBottom: function() {
		//订单列表流加载
		var that = this;
		if (that.isActive != "全部") {
			var status = that.isActive + 1;
		} else {
			var status = '';
		}
		var page = that.page;
		var type = that.type;
		var num = 10
		var timeStamp = Math.round(new Date().getTime() / 1000);
		var obj = {
			appid: appid,
			page: page + 1,
			timeStamp: timeStamp,
			type: type
		}
		var sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
		var data = {
			appid: appid,
			page: page + 1,
			timeStamp: timeStamp,
			num: num,
			sign: sign,
			type: type,
			status: status
		}
		if (uni.getStorageSync("is_child") == 0) {
			data.select_zid = that.childzid;
		}
		rs.getRequests("orderList", data, (res) => {
			if (res.data.code == 200) {
				if (res.data.data.list != '') {
					for (var i = 0; i < res.data.data.list.length; i++) {
						that.orderList.push(res.data.data.list[i])
					}
					that.is_look = res.data.data.is_look;
					that.page = page + 1;
				} else {
					that.load = false;
				}
			}
		})
	},
	onLoad() {
		app.aData.show = false;
		uni.hideTabBar();
	}
};
